import { useContext } from "react";
import { KeplrClient } from "./KeplrClient";
import { CONTRACT_ADDRESS } from "./KeplrClient";
import { Random } from "@iov/crypto";

const { client } = (KeplrClient);

export default class Secret {
    constructor() {
        this.show = false;
    }

    async store(code) {
        const token_id = Random.getBytes(16).reduce(
            (acc, b) => acc + b.toString(16).padStart(2, "0"),
            ''
        );

        return await client.execute(CONTRACT_ADDRESS, {
            "mint_nft": {
                token_id,
                public_metadata: {description: code}
            }
        });
    }

    async get(tokenId) {
        return await client.execute(CONTRACT_ADDRESS, {
            "burn_nft": {
                token_id: tokenId
            }
        });
        
        /*.then(res => {
            let result = JSON.parse(String.fromCharCode(...res.data))
            this.setState({ revealed: result?.burn_nft?.secret?.description});
        }).catch(err => {
            console.error(err)
        });
        */
    }
}
